/* General Styles */
body {
  font-family: 'Poppins', sans-serif;
  background-color: #f0f2f5;
  margin: 0;
  padding: 0;
  color: #333;
}

.container {
  width: 90%;
  max-width: 800px;
  margin: auto;
  padding: 20px;
}

/* Navbar */
.navbar {
  background: linear-gradient(135deg, #007bff, #0056b3);
  padding: 15px 20px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.nav-link {
  color: white;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  transition: 0.3s;
}

.nav-link:hover {
  text-decoration: underline;
  color: #ffdd57;
}

/* Buttons */
.button {
  display: inline-block;
  padding: 12px 20px;
  margin: 10px;
  border: none;
  background: linear-gradient(135deg, #28a745, #218838);
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  text-decoration: none;
  border-radius: 8px;
  transition: 0.3s ease-in-out;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.2);
}

.button:hover {
  background: linear-gradient(135deg, #218838, #1e7e34);
  transform: translateY(-2px);
  box-shadow: 2px 6px 15px rgba(0, 0, 0, 0.3);
}

.delete {
  background: linear-gradient(135deg, #dc3545, #b22234);
}

.delete:hover {
  background: linear-gradient(135deg, #b22234, #8b1a23);
}

/* Blog Post Cards */
.post-card {
  background: white;
  padding: 20px;
  margin: 20px auto;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  text-align: left;
  transition: 0.3s ease-in-out;
  width: 100%;
  max-width: 700px;
  border-left: 6px solid #007bff;
}

.post-card:hover {
  transform: scale(1.03);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
}

.post-card h2 a {
  text-decoration: none;
  color: #333;
  font-size: 22px;
  font-weight: bold;
}

.post-card h2 a:hover {
  color: #007bff;
}

/* Forms */
.form {
  background: white;
  padding: 25px;
  max-width: 500px;
  margin: 20px auto;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  text-align: left;
  border-top: 6px solid #007bff;
}

.input, .textarea {
  width: calc(100% - 20px);
  padding: 12px;
  margin-bottom: 15px;
  border: 2px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
  transition: 0.3s;
}

.input:focus, .textarea:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.2);
}

.textarea {
  height: 120px;
  resize: none;
}

.form h1 {
  text-align: center;
  font-size: 24px;
  color: #007bff;
}
